//Isotop

.filters-button-group {
  display: flex;
  justify-content: space-between;
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.6);
  border-top: solid 1px #383838;
  margin-bottom: 20px;
  button {
    background: none;
    outline: none;
    border: none;
    padding: 15px 20px;
    &:hover {
      color: $accent;
    }
    &:active {
      color: $accent;
    }
  }
  button.is-checked {
    color: $accent;
  }
  @media only screen and (max-width: 768px) {
    button {
      padding: 15px 10px;
    }
  }
  @media only screen and (max-width: 450px) {
    flex-wrap: wrap;
    justify-content: center;
    button {
      padding: 5px 10px;
    }
  }
}

.grid-filter {
  display: flex;
  margin-bottom: 50px;
  &:after {
    content: '';
    display: block;
    clear: both;
  }
  .product-item {
    position: relative;
    width: calc(25% - 20px);
    margin: 10px;
    @media only screen and (max-width: 1400px) {
      width: calc(33.33% - 20px);
    }
    @media only screen and (max-width: 768px) {
      width: calc(50% - 20px);
    }
    @media only screen and (max-width: 450px) {
      width: 50%;
      margin: 0 0 10px 0;
      border: none;
      background: none;
    }
  }
}
