.news-image {
  margin-bottom: 30px;
  border-radius: 40px;
  overflow: hidden;
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.6);
  height: 250px;
  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    @extend .animate
  }
}

.news-content {
  .news-title {
    font-size: 14px;
    line-height: 1.3;
    margin-bottom: 20px;
    a:hover {
      color: $accent;
    }
  }
  .date {
    font-size: 14px;
    font-family: $roboto;
    color: #666666;
    margin-bottom: 30px;
  }
  p, .excerpt{
    font-size: 14px;
    font-family: $roboto;
    line-height: 1.4;
    color: #999999;
    margin-bottom: 30px;
  }

  .news-more-link {
    font-size: 14px;
    color: $accent;
    &:hover {
      text-decoration: underline;
    }
  }
}

.news-grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  .news-article {
    width: 33.33%;
    .news-article-content {
      padding: 15px 15px 50px 15px;
    }

    &:hover .news-article-content .news-image img {
      transform: scale(1.1);
    }

    @media only screen and (max-width: 1400px) {
      width: 50%;

    }
    @media only screen and (max-width: 450px) {
      width: 100%;
    }
  }
}

.news-single {
  .news-image {
    float: left;
    margin-right: 30px;
    height: 300px;
  }
  .news-content {
    .news-title {
      font-size: 24px;
    }
  }
  @media only screen and (max-width: 1240px) {
    .news-image {
      float: none;
      margin-right: 0;
    }
  }
}
